import { useEffect, useState } from 'react';
import { Container, Row, Col, Nav } from 'react-bootstrap';
import { useParams } from 'react-router-dom';
import { addItem } from "./../store.js";
import { useDispatch } from 'react-redux';


function Detail(props) {
    let { id } = useParams();
    let selproduct = props.shoes.find((x) => x.id == id);

    let [tab, setTab] = useState(0);
    let [fade2, setFade2] = useState('');
    useEffect(() => {
        setFade2('end');
        return () => {
            setFade2('')
        }
    }, [])
    let dispatch = useDispatch()

    return (
        <Container className={'start ' + fade2}>
            <Row>
                <Col md={6}>
                    <img alt="sub_slider" src={"/img/shoes" + (Number(id) + 1) + ".jpg"} width="100%" />
                </Col>
                <Col md={6}>
                    <h4 className="pt-5">{selproduct.title}</h4>
                    <p>{selproduct.content}</p>
                    <p>{selproduct.price}</p>
                    <button className="btn btn-danger" onClick={() => {
                        dispatch(addItem({ id: selproduct.id, name: selproduct.title, count: 1 }))
                    }}>주문하기</button>
                </Col>
            </Row>

            <Nav variant="tabs" defaultActiveKey="link0">
                <Nav.Item>
                    <Nav.Link onClick={() => { setTab(0) }} eventKey="link0">버튼0</Nav.Link>
                </Nav.Item>
                <Nav.Item>
                    <Nav.Link onClick={() => { setTab(1) }} eventKey="link1">버튼1</Nav.Link>
                </Nav.Item>
                <Nav.Item>
                    <Nav.Link onClick={() => { setTab(2) }} eventKey="link2">버튼2</Nav.Link>
                </Nav.Item>
            </Nav>

            {/* {
                tab === 0 ? <div>내용0</div> : null
            }

            {
                tab === 1 ? <div>내용1</div> : null
            }

            {
                tab === 2 ? <div>내용2</div> : null
            } */}

            <TabContent tab={tab} />
        </Container>


    )
}

function TabContent({ tab }) {
    let [fade, setFade] = useState('');
    useEffect(() => {
        setTimeout(() => { setFade('end') }, 100)
        return () => {
            setFade('');
        }
    }, [tab])
    return (
        <div className={'start ' + fade}>
            {[<div>내용0</div>, <div>내용1</div>, <div>내용2</div>][tab]}
        </div>
    )
}

export default Detail;