import { Col } from 'react-bootstrap';

function Nike(props) {
    return (
        <Col md={4}>
            <img alt="item2" src={props.nike.imgUrl} width="80%" />
            <h5>{props.nike.title}</h5>
            <p>{props.nike.content}</p>
            <p>{props.nike.price}</p>
        </Col>
    )
}

export default Nike;