import { useState } from 'react';
import { Container, Navbar, Nav } from 'react-bootstrap';
import { Routes, Route, useNavigate } from 'react-router-dom'; 

import './App.scss';
import data from './data/data.js';

import Home from './route/Home';
import Detail from './route/Detail';
import Cart from './route/Cart';
import About from './route/About';
import Member from './route/Member';
import Location from './route/Location';
import Event from './route/Event';
import Footer from './component/Footer';

function App() {
  let [shoes, setShoes] = useState(data);
  let navigate = useNavigate();
  let [res, setRes] = useState([0, 1, 2, 3, 4, 5, 6, 7, 8]);

  return (
    <div className="App">
      <Navbar bg="dark" variant="dark">
        <Container>
          <Navbar.Brand onClick={() => { navigate('/') }}>ShoeShop</Navbar.Brand>
          <Nav className="me-auto">
            <Nav.Link onClick={() => { navigate('/') }}>Home</Nav.Link>
            <Nav.Link onClick={() => { navigate('/detail/0') }}>Detail</Nav.Link>
            <Nav.Link onClick={() => { navigate('/cart') }}>Cart</Nav.Link>
            <Nav.Link onClick={() => { navigate('/about') }}>About</Nav.Link>
            <Nav.Link onClick={() => { navigate('/event') }}>Event</Nav.Link>
            {/* <Link to="/">홈</Link>
            <Link to="/detail">상세페이지</Link>  */}
          </Nav>
        </Container>
      </Navbar>

      <Routes>
        <Route path="/" element={<Home shoes={shoes} setShoes={setShoes} res={res} setRes={setRes} />} />        
        <Route path="/detail/:id" element={<Detail shoes={shoes} />} />
        <Route path="/cart" element={<Cart />} />
        <Route path="/about" element={<About />}>
          <Route path="member" element={<Member />} />
          <Route path="location" element={<Location />} />
        </Route>
        <Route path="/event" element={<Event />}>
          <Route path="one" element={<div>첫 주문시 신발끈 서비스</div>} />
          <Route path="two" element={<div>생일기념 쿠폰 받기</div>} />
        </Route>
        <Route path="*" element={<div>없는 페이지입니다.</div>} />  {/* 404페이지 */}
      </Routes>

      <Footer />
    </div>
  );
}


export default App;
