import React from 'react';
import { useState } from 'react';
import { Container, Row, Button } from 'react-bootstrap';
import axios from 'axios';

import nike_data from '../data/nike_data.js';

import Title from '../component/Title.js';
import Product from '../component/Product.js';
import Title2 from '../component/Title2.js';
import Nike from '../component/Nike.js';

const Home = (props) => {
    let [nike, setNike] = useState(nike_data);
    let [count, setCount] = useState(1);

    return (
        <>
            <div className="slider">
              <img src={process.env.PUBLIC_URL + 'img/bg.jpg'} alt='slider'/>
            </div>

            <Title />

            <Button variant="outline-primary" className="btn" onClick={() => {
              let copy = [...props.shoes].sort((a, b) => a.title > b.title ? 1 : -1);

              props.setShoes(copy);

              let res_copy = [];
              for (let i in copy) {
                res_copy.push(copy[i].id)
              }

              props.setRes(res_copy);
            }}>이름순 정렬</Button>
            <Button variant="outline-secondary" className="btn" onClick={() => {
                let copy = [...props.shoes].sort((a, b) => a.price > b.price ? 1 : -1);

                props.setShoes(copy);
 
              let res_copy = [];
              for (let i in copy) {
                res_copy.push(copy[i].id)
              }

                props.setRes(res_copy);
            }}>낮은가격순 정렬</Button>
            <Button variant="outline-success" className="btn" onClick={() => {
                let copy = [...props.shoes].sort((a, b) => a.price < b.price ? 1 : -1);

                props.setShoes(copy);

              let res_copy = [];
              for (let i in copy) {
                res_copy.push(copy[i].id)
              }

                props.setRes(res_copy);
            }}>높은가격순 정렬</Button>

            <Container>
              <Row>
                {
                        props.shoes.map((v, i) => {
                    return (
                        <Product shoes={props.shoes[i]} i={i} res={props.res} key={i}></Product>
                    )
                  })
                }
              </Row>
            </Container>

            <Title2 />

            <Button variant="outline-success" count={count} onClick={() => {
              if (count === 1) {
                axios.get('https://jungwoonan.github.io/react_data/nike2.json').then((result) => {
                  let copy = [...nike, ...result.data];
                  setNike(copy);
                  setCount(2);
                })
              } else if (count === 2) {
                axios.get('https://jungwoonan.github.io/react_data/nike3.json').then((result) => {
                  let copy = [...nike, ...result.data];
                  setNike(copy);
                  setCount(3);
                })
              } else {
                alert("더이상 상품이 없습니다.");
              }
            }}> + 3개 상품 더 보기 </Button>

            <Container style={{marginTop:'30px'}}>
              <Row>
                {
                  nike.map((v, i) => {
                    return (
                      <Nike nike={nike[i]} key={i} />
                    )
                  })
                }
              </Row>
            </Container>
          </>
    );
};

export default Home;